<template>

    <div class="appBox">
        <div class="bg"></div>
        <p class="grade">您当前的级别: {{userInfo.vip_level}}级</p>
        <div class="balanceBox">
            <p class="usdtBalance">{{treeInfo.currency_name}}余额：{{userInfo.money}}</p>
            <p class="state">升级到</p>
            <div class="stateBox ov">
                <p class="fl pr">
                    <span class="line"></span>
                    <span class="vg1">v{{userInfo.vip_level}}</span>
                    <span class="vg2" v-if="userInfo.vip_level < treeInfo.level">v{{tolevel}}</span>
                    <span class="vg2" v-else>v{{ treeInfo.level}}</span>
                </p>
                <i @click="addLevel" class="fl"></i>
            </div>
            <div class="fast" v-if="userInfo.vip_level < treeInfo.level">
                <span>升到{{tolevel}}级需要缴纳：{{upgradMoney}}{{treeInfo.currency_name}} </span>
                <p @click="upgrade">立即升级</p>
            </div>
            <div class="fast" v-else>
                <p>已满级</p>
            </div>
        </div>
        <!--  公共组件  -->
        <tree-bar :tree_id="tree_id"></tree-bar>
    </div>
</template>

<script>
    import treeBar from '@/page/tree/treeBar'
    import { upgradeInfo, upgrade } from '@/api/tree'
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    import Dialog from 'vant/lib/dialog';
    import 'vant/lib/dialog/style';
    import fmath from '@/utils/math'
    export default {
        name: "Treeupgrade",
        components: {
            treeBar
        },
        data()
        {
            return {
                tree_id:'',
                treeInfo:{},
                userInfo:{},
                tolevel:0,
                upgradMoney:0
            }
        },
        watch:
        {
            tolevel(val)
            {

                // 限制最高级别
                if (this.tolevel > this.treeInfo.level)
                {
                    this.tolevel =  this.treeInfo.level
                }
                // 计算升级价格
                let upmoney = 0
                for (let i = 0; i < this.tolevel; i++)
                {
                     if (i < this.userInfo.vip_level)
                     {
                         continue
                     }

                    upmoney = fmath.floatAdd(upmoney, this.treeInfo.upgrade_money[i])
                }

                this.upgradMoney = upmoney

            }
        },
        created()
        {
            this.tree_id = this.$route.query.treeId
        },
        mounted()
        {
            this.getUpgradeInfo()
        },
        methods:{

            // 财富树信息
            getUpgradeInfo()
            {
                upgradeInfo({tree_id:this.tree_id}).then(res => {

                    this.userInfo = res.userInfo
                    this.treeInfo = res.tree

                    if (this.userInfo.vip_level < this.treeInfo.level)
                    {
                        this.tolevel = this.userInfo.vip_level + 1

                    }
                })
            },
            // 调整级数
            addLevel()
            {
                if (this.tolevel < this.treeInfo.level)
                {
                    this.tolevel++
                } else
                {
                    Toast('已是最高级别')
                }
            },
            // 升级
            upgrade()
            {
                Dialog.confirm({
                    title: '系统提醒',
                    message: '升级到'+this.tolevel+'级将消耗'+this.upgradMoney+this.treeInfo.currency_name+', 确定升级么?'
                }).then(() => {
                    // 确认
                    upgrade({tree_id:this.tree_id, tolevel:this.tolevel}).then(res => {

                        this.userInfo.vip_level = this.tolevel
                        this.tolevel = this.tolevel + 1
                        this.userInfo.money = res.money
                    })

                }).catch(() => {

                    // 取消
                });
            }
        }
    }
</script>

<style scoped>
    @import "../../static/css/treeupgrade.css";
</style>



